.main {
  margin: 0 auto;
  padding: 0;
}

.blog-post {
  max-width: 780px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 880px) {
  .blog-post {
    padding: 0;
  }
}

aside {
  position: sticky;
  top: 0;
}
aside > div.fix {
  position: fixed;
  top: 190px;
}
aside h3 {
  font-size: 14px;
  line-height: 17px;
}
aside li {
  margin-bottom: 16px;
  font-size: 13px;
  line-height: 16px;
}
aside .social h3 {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #000624;
}

main {
  max-width: 780px;
  width: 100%;
}
main h1 {
  font-weight: bold;
  font-size: 24px;
  line-height: 35px;
  color: #000624;
  margin-bottom: 1px;
  display: flex;
  align-items: center;
}
main h1 button {
  height: fit-content;
  margin-top: 5px;
  margin-left: 8px;
}
@media (min-width: 880px) {
  main h1 button {
    display: none;
  }
}
main > p {
  font-size: 16px;
  line-height: 24px;
  color: #5f5f68;
}

.post-page .date {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
}
.post-page--head {
  display: flex;
  width: 100%;
  flex-flow: column;
}
@media (min-width: 880px) {
  .post-page--head {
    flex-flow: row;
    align-items: center;
    justify-content: space-between;
  }
}
.post-page--head button {
  padding: 0;
  font-weight: 500;
}
.post-page--head .breadcrumb {
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  color: #31323d;
}
.post-page--head .breadcrumb a {
  font-weight: 500;
}
.post-page--head .breadcrumb__tag {
  color: #477db3;
}
.post-page--head .breadcrumb--divider {
  margin-left: 16px;
  margin-right: 16px;
}
.post-page--head .date {
  font-size: 14px;
  line-height: 24px;
  color: #000624;
  display: flex;
  margin-top: 16px;
}
.post-page--head .date span {
  margin: 0 7px 2px 7px;
}
.post-page--head .date time {
  font-size: 14px;
}
@media (min-width: 880px) {
  .post-page--head .date {
    margin-top: unset;
  }
}
.post-page--loader {
  width: 80%;
  left: 10%;
  height: 5px;
  background: #e6e6e6;
  position: sticky;
  margin-bottom: 52px;
  overflow: hidden;
}
.post-page--loader.fix {
  position: fixed;
  max-width: 1237px;
  top: 90px;
  z-index: 5;
}
.post-page--loader div {
  position: absolute;
  background: #5cc685;
  left: 0;
  height: 100%;
}
.post-page--body {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #31323d;
}
.post-page--title {
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: #000624;
  margin: 16px 0 40px 0;
}
@media (min-width: 880px) {
  .post-page--title {
    font-weight: 700;
    font-size: 48px;
    line-height: 58px;
    margin: 31px 0 25px 0;
  }
}
.post-page--title.small {
  font-size: 24px;
}
.post-page--author {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 56px;
}
@media (min-width: 880px) {
  .post-page--author {
    margin-bottom: 0px;
  }
}
.post-page--author .author {
  display: flex;
  align-items: flex-start;
}
.post-page--author .author img {
  width: 100% !important;
  border-radius: 50%;
  margin-right: 12px;
}
.post-page--author .img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #f5f5f5;
  margin-right: 16px;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.post-page--author h5 {
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  color: #477db3;
  margin-bottom: 3px;
}
.post-page--author p {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #31323d;
  margin-bottom: 7px;
}
.post-page--author .socials li {
  width: 32px;
  height: 32px;
}

.more-posts {
  padding: 0 20px;
  max-width: 970px;
  margin: 80px auto 0;
}
@media (min-width: 880px) {
  .more-posts {
    margin-top: 130px;
  }
}
.more-posts h1 {
  font-weight: bold;
  font-size: 32px;
  line-height: 130%;
}
.more-posts .posts {
  justify-content: center;
  margin-top: 0px;
}